  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {          
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}



/* My styles */
body {
  background-color: #000;
  color: #fff;
  font-family: "Ubuntu Mono";
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: 100dvh;
  width: max(calc(14ch * 2 + 10px), 100%);
  align-items: center;
  padding: 5px;
  font-size: 1.2rem;
}

main { 
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 100%;
}
h1, h2 {
  font-weight: 400;
}

button {
  background: none;
  color: inherit;
  border: none;
  &:hover { cursor: pointer; }
}

header {
  display: flex;
  flex-direction: column;
  align-items: center; 
}

.controls {
  display: flex;
  justify-content: center;
  gap: 5px;
  & button {
    width: 8ch;
    border-radius: 2px;
  }
  /* this will be set with JS when User presses a btn */
  .active { 
    background-color: rgb(255 255 255 / 0.2);
  }
  
  button {
    background-color: rgb(255 255 255 / 0.05);
  }
}

.clock {
  font-size: 5rem;
  text-align: center;
}

.timer {
  & .start,
  .pause {
    font-size: 2rem;
    width: 100%;
    text-align: center;
    & > button { 
      background-color: rgb(255 255 255 / 0.05); 
      border-radius: 2px;
      padding: 0 1em;
    }
  }
}

.tasks-title-wrapper {
  position: relative;
  margin-bottom: 16px;
}
h2 {
  text-align: center;
  /* text-decoration didn't look nice in Chromium-based browsers, so I made a custom underline */
  & + .underline {
    position: absolute;
    bottom: 5px;
    left: calc(50% - 2.5ch * 1.5);
    width: calc(5ch * 1.5);
    height: 1px;
    background-color: #fff;
  }
}

ul {
  list-style-type: none;
  padding: 0;
}

.tasks {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 3px;
}

.task {
  width: 100%;
  .main {
    display: flex;
    gap: 1ch;
    align-items: first baseline;
    .check {
      padding: 0; 
      min-width: 3ch;
    }
    .name {
      font-size: inherit;
      font-weight: inherit;
      overflow-wrap: break-word;
      width: calc(100% - 13ch - 20px);
      flex-grow: 1;
      &:hover { cursor: pointer; }
    }
    .tomatoes {
      margin-left: auto;
      flex-wrap: nowrap;
      &:hover {
        cursor: pointer;
      }
    }
    .edit-task {
      height: 1rlh; 
      position: relative;
      top: 3px;
      flex-shrink: 0;
    }
  }
  .note {
    opacity: 0.5;
    padding: 0 4ch; 
    &:hover {
      cursor: pointer;
    }
  }
}

.current-task {
  background-color: rgb(255 255 255 / 0.2);
  border-radius: 2px;
}

