.new-task,
.task-edit {
  background-color: rgb(255 255 255 / 0.2);
  border-radius: 5px;
  padding: 0.5rem 6ch 1rem 4ch; 
  form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    & input[type="text"] {
      width: 100%;
      background: none;
      border: none;
      color: #fff;
      &::placeholder {
        color: rgb(255 255 255 / 0.5);
        font-style: italic;
      }
      &:focus {
        outline: none;
      }
    }
    .est-tomatoes-wrapper {
      label:has(+ &) { margin-bottom: 0.2rem; }
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 0.5rem;
      & input { 
        width: 5rem;
        flex-shrink: 1; 
        margin-right: 0.5rem;
        border: none;
        outline: none;
      }
    }
    .tomatoes-wrapper {
      display: flex;
      gap: 16px;
      & > * {
        align-items: center;
        display: flex;
        gap: 0.5rem;
        input { 
          width: 3rem; 
          margin-right: 0.5rem;
          border: none;
          outline: none;
        }
      }
    }
    .up, .down {
      padding: 5px;
      background-color: #fff;
      border-radius: 2px;
      flex-shrink: 0;
    }
    .add-note {
      margin-top: 8px;
      padding-left: 0;      
    }
    .note-wrapper { 
      position: relative;
      width: 80%;
    }
    .delete:has(+ .note) {
      position: absolute;
      top: 18px;
      right: 3px;
      content: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
      width: 15px;
      height: 15px;
      z-index: 1;
      &:hover {
        cursor: pointer;
      }
    }
    .note {
      margin: 16px 0;
      padding: 20px 20px 0 0;
      background-color: white;
      & > textarea {
        width: 100%;
        height: 100%;
        border: none;
        outline: none;
        resize: none;
      }
    }
    .form-submit-wrapper {
      width: 100%;
      display: flex;
      justify-content: flex-end;
      gap: 1rem;
      & > * {
        width: 6ch;
        padding-left: 0;
      }
      & > .delete-task {
        margin-right: auto;
      }
    }
  }
}

.create-task {
  font-size: 2rem;
  align-self: center;
  background-color: rgb(255 255 255 / 0.05);
  padding: 0 16px;
  border-radius: 2px;
}

footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-bottom: 16px;
  .progress {
    display: flex;
    align-items: first baseline;
  } 
  .progress-bar {
    height: 1ex;
    width: calc((6 / 20) * 100%);
    background-color: #fff;
  }
  .right-brace {
    margin-left: auto;
  }
}

.settings {
  position: absolute;
  z-index: 2;
  background-color: #808080;
  height: 100dvh;
  width: 100dvw;
  top: 0; 
  right: 0;
  & > form {
    margin-top: 16px; 
    display: flex;
    flex-direction: column;
    padding: 0 1rem;
    & .setting-controls {
      display: flex;
      flex-direction: column;
      align-items: start;
      & > button {
        padding: 0;
      }
    }
  }
}

@media (min-width: calc(14rem + 110px)) {
  body {
    gap: 16px;
  }
  header {
    margin-right: auto;
  }
  .menu-btn {
    position: absolute;
    top: 10px;
    right: 0;
    z-index: 3;
  }
  .clock {
    font-size: 6rem;  
    line-height: 1;   
  }
}

@media (min-width: 26rem) {
  header { margin-right: 0; }
}

@media (min-width: 65ch) {
  body {
    padding: 0 calc((100vw - 65ch) / 2);
  }
  header {
    width: 100%;
  }
  /* h1 {
    align-self: flex-start;
  } */
  .menu-btn {
    right: calc((100vw - 65ch) / 2);
  } 
  footer { padding: 0 6ch 200px 4ch; }
  .settings {
    top: 7px;
    right: calc((100vw - 65ch) / 2);
    width: 30ch;
    height: 75%;
    border-radius: 5px;
  }
} 

